#contact {
	padding: 5rem 0;
}
#contact .wrapper {
	display: flex;
	align-items: flex-start;
	justify-content: center;
	gap: 5rem;
}
#contact .wrapper .left {
	flex: 1;
	justify-content: center;
	padding: 0 2vw;
}
#contact .wrapper .right {
	flex: 1;
}
#contact .left .contact-heading {
	font-family: 'Handlee', cursive;
	font-size: 3rem;
	margin-bottom: 1rem;
	text-align: center;
}
#contact .left .contact-desc {
	font-family: 'Handlee', cursive;
	font-size: 1.5rem;
	color: var(--secondary-gray);
	margin-bottom: 2rem;
	text-align: center;
}

#contact .right .form-wrapper {
	background-color: #0e0c13;
	max-width: 100%;
	margin: 0 auto;
	padding: 5rem;
	border-radius: 12px;
	color: white;
}

#contact .right .form-group {
	margin-bottom: 1rem;
}
#contact .right .form-group label {
	margin-bottom: 0.5rem;
	display: inline-block;
	font-size: 1.8rem;
}
#contact .right .form-group .input-wrapper {
	background-color: white;
	color: black;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	height: 40px;
	border-radius: 4px;
	overflow: hidden;
}

#contact .right .form-group .input-wrapper .icon {
	font-size: 1.5rem;
	height: 100%;
	width: 50px;
	background-color: rgb(218, 218, 218);
	display: flex;
	align-items: center;
	justify-content: center;
}
#contact .right .form-group .input-wrapper input {
	padding: 1rem;
	height: 100%;
	width: 100%;
	outline: none;
	border: none;
	font-size: 1.5rem;
}
#contact .right .form-group textarea {
	width: 96%;
	height: 150px;
	resize: vertical;
	font-size: 1.5rem;
	padding: 1rem;
	outline: none;
	border-radius: 4px;
}

#contact .right .form-wrapper button {
	display: inline-block;
	width: 150px;
	font-size: 1.8rem;
	text-transform: uppercase;
	height: auto;
	padding: .5rem 0;
	font-family: 'Roboto Mono';
	letter-spacing: 0.1rem;
	border-radius: 7px;
	outline: none;
	border: none;
	cursor: pointer;
	background-color: rgb(29, 82, 255);
	color: white;
}
#contact .right .form-wrapper button:hover{
	background-color: rgb(160, 181, 250);
}

@media only screen and (max-width: 768px) {
	#contact .wrapper {
		flex-direction: column-reverse;
		justify-content: center;
		align-items: center;
	}
	#contact .right {
		width: 100%;
	}
	#contact .right .form-wrapper {
		padding: 3rem;
		width: 100%;
		max-width: 100%;
	}
}