.scroll-top {
	z-index: 100;
	height: 60px;
	width: 60px;
	background-color: var(--primary-color);
	position: fixed;
	bottom: 10px;
	right: 10px;
	cursor: pointer;
	border-radius: 6px;
	box-shadow: 0px 0px 20px rgba(71, 40, 182, 0.226);
	transform: scale(0.9);
	transition: 0.3s ease-in-out transform;
	align-items: center;
	justify-content: center;
}

.scroll-top svg {
	color: white;
	pointer-events: none;
}

.scroll-top:hover {
	transform: scale(1);
}