#footer {
	background-color: #090113;
	color: white;
	padding: 10rem 0 5rem 0;
    margin-top: 10rem;
}
#footer footer {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	gap: 8rem;
}
#footer footer .col-1 {
	flex: 3;
}
#footer footer .col-2,
#footer footer .col-3 {
	flex: 2;
}
#footer footer .col-3 {
	flex: 1;
}
#footer footer .col-1 .logo img {
	max-width: 100px;
	margin-bottom: 2rem;
}
#footer footer .col-1 p {
	color: white;
}
#footer footer .column-heading {
	font-family: 'Montserrat';
	font-size: 2rem;
	margin-bottom: 2rem;
	text-transform: uppercase;
	line-height: 1.5em;
}
#footer footer ul {
	list-style: none;
}
#footer footer ul li {
	border-radius: 4px;
}
#footer footer ul li:hover {
	background-color: white;
}
#footer footer ul li:hover .link {
	color: var(--primary-color);
}
#footer footer ul li .link {
	display: inline-block;
	width: 100%;
	color: white;
	font-size: 1.7rem;
	padding: 0.5rem;
}
#footer footer .col-3 ul {
	display: flex;
	flex-wrap: wrap;
}
#footer footer .col-3 ul li{
    padding: 0.5rem;
}
#footer footer .col-3 ul li a{
	min-width: 60px;
    color: white;
	font-size: 1.7rem;
    padding: 0.5rem;
}
#footer footer .col-3 ul li a:hover{
    color: var(--primary-color);
}



@media only screen and (max-width: 768px) {
    #footer{
        margin-top: 20rem;
    }
	#footer footer {
		flex-direction: column;
		justify-content: center;
		text-align: center;
	}
	#footer footer .col-1,
	#footer footer .col-2,
	#footer footer .col-3 {
		width: 100%;
		margin-bottom: 3rem;
	}
	#footer footer .col-3 ul {
		width: fit-content;
		margin: 0 auto;
	}
}

#copyright {
	padding: 1rem 0;
	background-color: #100a24;
}
#copyright p {
	color: white;
	margin: 1rem;
	font-size: 1.4rem;
}
@media only screen and (max-width: 768px) {
	#copyright p {
		text-align: center;
	}
}